import React from "react";
import { BigContainer } from "../../../style-app";
import styles from "./style.module.css";
import { Row, Col } from "react-grid-system";
import { useTranslation } from "react-i18next";

import catalog1 from "../../../assets/home/catalog1.png";
import catalog2 from "../../../assets/home/catalog2.png";
import catalog3 from "../../../assets/home/catalog3.png";
import catalog4 from "../../../assets/home/catalog4.png";
import { NavLink } from "react-router-dom";

const HomeCatalog = () => {
  const {t} = useTranslation()
  return (
    <>
      <div className={styles.home_catalog_section}>
        <BigContainer>
          <div className={styles.title_box}>
            <h2>{t("Header.0")}</h2>
          </div>
          <Row className={styles.catalog_row}>
            <Col className={styles.catalog_col} lg={8} md={12} sm={12} sx={12}>
              <NavLink className={styles.catalog_col_box_link} to="/catalog">
                <img src={catalog1} alt="catalog-img" />
              </NavLink>
            </Col>
            <Col className={styles.catalog_col} lg={4} md={12} sm={12} sx={12}>
              <div className={styles.catalog_col_box}>
                <NavLink className={styles.catalog_col_box_link} to="/catalog">
                  <img src={catalog2} alt="catalog-img" />
                </NavLink>
              </div>
              <div className={styles.catalog_col_wrapp}>
                <NavLink className={styles.catalog_col_box_links} to="/catalog">
                  <img
                    className={styles.catalog_img}
                    src={catalog3}
                    alt="catalog-img"
                  />
                </NavLink>
                <NavLink className={styles.catalog_col_box_links} to="/catalog">
                  <img
                    className={styles.catalog_img}
                    src={catalog4}
                    alt="catalog-img"
                  />
                </NavLink>
              </div>
            </Col>
          </Row>

          <Row className={styles.catalog_row_mobile}>
            <Col
              className={styles.catalog_col_mobile}
              lg={6}
              md={6}
              sm={6}
              sx={6}
            >
              <NavLink className={styles.catalog_col_box_link_mobile} to="/catalog">
                <img src={catalog1} alt="catalog-img" />
              </NavLink>
            </Col>
            <Col
              className={styles.catalog_col_mobile}
              lg={6}
              md={6}
              sm={6}
              sx={6}
            >
              <NavLink className={styles.catalog_col_box_link_mobile} to="/catalog">
                <img src={catalog2} alt="catalog-img" />
              </NavLink>
            </Col>
            <Col
              className={styles.catalog_col_mobile}
              lg={6}
              md={6}
              sm={6}
              sx={6}
            >
              <NavLink className={styles.catalog_col_box_link_mobile} to="/catalog">
                <img src={catalog2} alt="catalog-img" />
              </NavLink>
            </Col>
            <Col
              className={styles.catalog_col_mobile}
              lg={6}
              md={6}
              sm={6}
              sx={6}
            >
              <NavLink className={styles.catalog_col_box_link_mobile} to="/catalog">
                <img src={catalog1} alt="catalog-img" />
              </NavLink>
            </Col>
          </Row>
        </BigContainer>
      </div>
    </>
  );
};

export default HomeCatalog;
